<template>
  <v-container id="about" fluid fill-height style="max-height: 100vh;">
    <v-btn
        elevation="2"
        icon
        outlined
        fab
        @click="open('github')"
    >
      <img src="../assets/github_logo.png" width="32" height="32">
    </v-btn>
    <v-btn
        elevation="2"
        icon
        outlined
        fab
        @click="open('linkedin')"
    >
      <img src="../assets/linkedin_logo.png" width="32">
    </v-btn>
  </v-container>
</template>

<script>
export default {
  name: "About",
  methods: {
    open(link) {
      switch (link) {
        case "github":
          window.open("https://github.com/Jlamon", "blank_");
          break;
        case "linkedin":
          window.open("https://be.linkedin.com/in/julien-lamon-074276171", "blank_");
          break;
      }
    }
  }
}
</script>

