
<template>
  <div>
    <v-app-bar
        permanent
        app
        color="white"
        elevate-on-scroll
    >
      <v-toolbar-title v-on:click='scrollTo("home")'><span class="flip">J</span><span class="flip">u</span>
        <span class="flip">l</span><span class="flip">i</span><span class="flip">e</span><span class="flip">n</span>
        <span class="flip"> L</span><span class="flip">a</span><span class="flip">m</span><span class="flip">o</span>
        <span class="flip">n</span><span class="flip">.</span></v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn v-on:click='scrollTo("home")' plain>
        Home
      </v-btn>
      <v-btn v-on:click='scrollTo("about")' plain>
        About
      </v-btn>
      <v-btn v-on:click='scrollTo("projects")' plain>
        Projects
      </v-btn>
      <v-btn outlined color="error" v-on:click='scrollTo("resume")' plain>
        Resumé
      </v-btn>
    </v-app-bar>
  </div>
</template>

<script>
export default {
  name: "NavBar",
  methods: {
    scrollTo(id){
      document.getElementById(id).scrollIntoView({behavior: 'smooth'});
    }
  }
}
</script>

<style>
  span {
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    -webkit-transform: perspective(250px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    transform: perspective(250px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    display:inline-block;
  }
  span.flip:hover {
    -webkit-transform: perspective(250px) rotateX(360deg) rotateY(0deg) rotateZ(0deg);
    transform: perspective(250px) rotateX(360deg) rotateY(0deg) rotateZ(0deg);
  }
</style>